<template>
  <div class="register" style="background-color: #fff;">
    <div class="register-bar">
      <van-nav-bar :title="$t('UnderReview')" left-arrow @click-left="linkHome"/>
    </div>
    <div class="submission">
      <div class="submission-img">
        <img src="../../../assets/examine/submission.png"/>
      </div>
      <div class="tips">
        <div>{{$t('ReviewTips1')}}</div>
        <div>{{$t('ReviewTips2')}}</div>
        <div>{{$t('ReviewTips3')}}</div>
      </div>
      <div class="steps">
        <van-steps direction="vertical" :active="1" active-color="#1988FF">
          <van-step>
            <img slot="inactive-icon" class="step-active" src="../../../assets/examine/stepsOk.png" />
            <div style="color: #1988FF">{{$t('AuditProcess1')}}</div>
          </van-step>
          <van-step>
            <img slot="active-icon" class="step-active" src="../../../assets/examine/stepsOk.png" />
            <div>{{$t('AuditProcess2')}}</div>
          </van-step>
          <van-step class="lastClass">
            <img slot="inactive-icon" class="step-active step-inactive" src="../../../assets/examine/steps3.png" />
            <div>{{$t('AuditProcess3')}}</div>
          </van-step>
        </van-steps>
      </div>
      <div class="again" @click="linkHome">{{$t('backHome')}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'toBeAudited',
  methods: {
    // 返回上级
    linkHome () {
      this.$router.push({
        name:'home'
      })
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../../../style/register";
</style>



